import { ref, query, orderByChild, orderByValue, child, limitToLast, get, onValue, onChildAdded, equalTo } from "firebase/database";
import { getDatabase } from "firebase/database";

// const consultarank = query(ref(database, '/'), orderByChild('modos'));

export default class ControleRank {

  constructor(firebase, auth) {
    //Carregando bibliotecas instanciadas no app central
    this.auth = auth //Autenticaca: auth.auth (firebase auth)
    this.firebase = firebase

    //Carregando base de dados 
    this.db = getDatabase(firebase);

    //Carregando nó raiz
    this.database = ref(this.db, 'placar')

    //Controle
    this.controleRank = 0
    this.limiteRank = 5

    //Query de obtenção da lista de rank 
    this.consultarank = query(this.database, orderByChild('pontuacao'), limitToLast(this.limiteRank));
  }

  // Conectando Pai a atualização do filho
  ancora(forceUpdate) {
    console.log("WHAT",forceUpdate);
    this.forceUpdate = forceUpdate

  }
  update(item) {
    this.forceUpdate(item);
  }

  rank() {
    console.log("RANKING");
 
    onValue(this.consultarank, (snapshot) => {
      if (snapshot.exists()) {
        console.log("Retorno Rank", snapshot);
        this.ordernarRank(snapshot)

      } else {
        console.log("Vazio");

      }
    }, { onlyOnce: true });
    console.log(this.consultarank.toJSON())
  }


  ordernarRank(lista){
    let rank = []
    lista.forEach((element) =>{
    console.log(element);
    let item = element.val()
    item['key'] = element.key
    console.log(item)
    rank.push(item)
    })
    rank.sort(function(a, b){return b['pontuacao']-a['pontuacao']});
    this.update(rank)
  }









}