import { getAuth, onAuthStateChanged } from "firebase/auth";

import usuario from "./usuario";




export default class ControleRank {

    constructor() {
        this.auth = getAuth();
        this.usuario = null
    }

    /////// Controle de Autenticação inicial /////
    // Conectando Pai a atualização do filho /////
    ancora(forceUpdate) {
        console.log("WHAT",forceUpdate);
        this.forceUpdate = forceUpdate

    }
    update(item) {
        this.forceUpdate(item);
    }

    //////////////////////////////////////////////

    init() {
        onAuthStateChanged(this.auth, (user) => {
            console.log("Autenticação: User corrente");
            console.log("USER: ", user);
            if (user !== null) {
                setTimeout(() => {  this.update(true) }, 2000);
                this.userCreate()
            }
            else{
                setTimeout(() => {  this.update(false) }, 2000);
                this.userCreate()
            }
            

        })
    }    
    ///////////////////////////////////////////////


    userCreate(){
        
        if (this.verifyUser()) {
            console.log(this.auth.currentUser);
            console.log("Criando USER ----:", this.auth.currentUser.displayName );
      
            this.usuario = new usuario(this.auth.currentUser)
      
      
            console.log("Usuario Criado", this.usuario);
            
            console.log("Depois de atualizar o user");
          } else 
              this.usuario = null
    }

    verifyUser() {

        const user = this.auth.currentUser;
        if (user !== null)
            return true
        else
            return false
    }

    








}